.container {
    display:flex;
    width:100%;
    height:100%;
    justify-content:center;
    background-color:white;



}


.img-container {
    display:flex;
    align-items: center;
    width:30vw;
    height:100vh;
    background-color:white;
}

.artwork {
    height:100vh;
    background-color:white;
}

.test {
    height:100vh!important;
    background-color:white!important;
}

@media screen and (max-width: 768px) {
    
    .img-container {
        display:none
    } 


}

@media screen and (min-width: 769px) {
    .mobile-msg {
        display:none
    }
}

.mobile-msg {
    padding-bottom: 10vh;
    width:70vw;
    font-family:'Roboto';
    font-size:1.5vh;
    letter-spacing: 1px;
    text-align:center;
}